import uniPopup from "../../../components/uni-popup/uni-popup.vue"
import uniCalendar from '../../../components/uni-calendar/uni-calendar.vue'
import uniNumberBox from '../../../components/uni-number-box.vue'
export default {
	data() {
		const currentDate = this.getDate({
			format: true
		})
		return {
			index: 0, //门票信息的数组值
			number: 0, //门票数量的值
			actualPayment: '', //实际付款
			selectedValue: 0, //同意须知的选中值
			dateReminder: '明天', //日期提醒
			date: currentDate, //默认时间
			maskState: 0, //优惠券面板显示状态
			payType: 1, //1微信 2支付宝
			scSpotDetails: {}, //景区内容
			couponIndex: '请选择优惠券', //优惠券默认内容
			couponColor: '', //优惠券默认数值，大于等于0触发价格判断事件
			couponList: [{
					couponID: '0',
					title: '新用户专享优惠券',
					price: 5,
					condition: 10,
				},
				{
					couponID: '1',
					title: '春节限时限量优惠券',
					price: 10,
					condition: 100,
				},
				{
					couponID: '2',
					title: '大型团购优惠券-今点通限量版',
					price: 300,
					condition: 1000,
				}
			],
			addressData: {
				name: '许小星',
				idCard: '35058199503692367',
				mobile: '13853989563',
				addressName: '金九大道',
				address: '山东省济南市历城区',
				area: '149号',
				default: false,
			},
		}
	},

	onLoad(option) {
		this.lyfwData();
		this.daysAddSub();
	},

	components: {
		//加载多方弹框组件
		uniPopup,
		//加载日期组件 
		uniCalendar,
		//加载增减数量组件
		uniNumberBox,
	},
	methods: {
		//读取静态数据
		async lyfwData() {
			let scSpotDetails = await this.$api.lyfw('scSpotDetails');
			this.scSpotContent = scSpotDetails;
			console.log(this.scSpotDetails)
		},

		//显示优惠券面板
		toggleMask(type) {
			let timer = type === 'show' ? 10 : 300;
			let state = type === 'show' ? 1 : 0;
			this.maskState = 2;
			setTimeout(() => {
				this.maskState = state;
			}, timer)
		},

		//优惠券赋值
		couponEvent: function(index) {
			if (this.actualPayment >= this.couponList[index].condition) {
				this.couponIndex = '-' + this.couponList[index].price;
				this.couponColor = this.couponList[index].couponID;
				this.calcTotal();
				this.toggleMask();
			} else {
				uni.showToast({
					title: '您的实付款未达到条件，请重新选择',
					icon: 'none'
				})
			}

		},

		//取消优惠券
		couponReset: function(index) {
			this.couponIndex = '请选择优惠券';
			this.couponColor = '';
			this.calcTotal();
			this.toggleMask();
		},

		changePayType(type) {
			this.payType = type;
		},



		//提交表单
		submit: function() {
			if (this.selectedValue == 1) {
				uni.redirectTo({
					url: '/pages/money/pay'
				})
			} else {
				uni.showToast({
					title: '请同意购买须知',
					icon: 'none'
				})
			}
		},

		stopPrevent() {},

		//打开选择器
		open() {
			this.$refs.calendar.open();
		},

		//打开popup下弹框
		open2(e) {
			if (e == 1) {
				this.$refs.popup1.open()
			} else {
				this.$refs.popup2.open()
			}
		},

		close(e) {
			if (e == 1) {
				this.$refs.popup1.close()
			} else {
				this.$refs.popup2.close()
			}
		},



		//获取当前时间并格式化
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();
			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day+1}`;
		},


		//时间选择器，确认选择
		confirm(e) {
			this.getTime(e);
		},

		//时间效验，已提示
		getTime: function(e) {
			let date = new Date(),
				day = date.getDate(),
				second = date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
			day >= 0 && day <= 9 ? (day = "0" + day) : "";
			var index = e.date - day;
			if (index < 0) {
				uni.showToast({
					title: '请勿选择以往日期',
					icon: 'none'
				})
			} else if (index == 0) {
				this.dateReminder = '今天'
				this.date = e.fulldate;
			} else if (index == 1) {
				this.dateReminder = '明天'
				this.date = e.fulldate;
			} else if (index == 2) {
				this.dateReminder = '后天'
				this.date = e.fulldate;
			} else {
				this.dateReminder = e.lunar.ncWeek;
				this.date = e.fulldate;
			}
		},

	}
}
